$( document ).ready(function() {
	var people = $(".ppl img")
	var bannerleft = $(".banner-left")
	var bannerright = $(".banner .banner-right")
	var bannertitle = $(".banner-title")
	var section = $("section.event-2018-may")
	
	$(window).on("load",function(){
	
		$("section.event-2018-may, .banner-left, .banner-right, .ppl img, .banner-title").addClass("active")
	
		setTimeout(function () {
			bannerleft.removeClass("active")
			bannerleft.addClass("static") 
	
			bannertitle.removeClass("active")
			bannertitle.addClass("static")
	
			people.removeClass("active")
			people.addClass("static")
	
			section.removeClass("active")
			section.addClass("static")
	
			bannerright.removeClass("active")
			bannerright.addClass("static")
	
			var windowW = $(window).width()
			function mouseEvent(evt) {
				var winW = $(window).width()
				var winH = $(window).height()
				var gridX = winW/5
				var gridY = winH/2
				var pagex = evt.pageX / gridX - 2.5;
				var pagey = -evt.pageY / gridY + 1;
				
				var valueX = pagex * 10;
				var valueY = pagey * 10;
				
				var logoX = pagex * 2.5;
				var logoY = pagey * 2.5;
	
				bannerleft.css("transform", "translateX("+(valueX)+"px)")
				bannerright.css("transform", "translateX("+(-valueX)+"px)")
				people.css("transform", "translateX("+(logoX)+"px)")
			}
			if (windowW >= 992){
				$(".event-2018-may").on("mousemove", mouseEvent)
			} else {
				$(".event-2018-may").off("mousemove", mouseEvent)
			}

			$(window).on("resize", function () {
				windowW = $(window).width()
				if (windowW >= 992){
					$(".event-2018-may").on("mousemove", mouseEvent)
				} else {
					$(".event-2018-may").off("mousemove", mouseEvent)
				}
			})
		}, 4500)
	});		
	
})

